html,
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;
}

.header {
  padding: 1rem;
}

.weatherSection {
  padding: 1rem;
}

.weatherWrapper {
  width: 100vw;
  overflow-x: scroll;
}

.weatherWrapper svg {
  padding: 0 30px 0 0;
}

.subtitle {
  text-transform: uppercase;
  color: #999;
  font-size: 12px;
  margin: 0 0 0.2;
}

h1,
h2 {
  margin: 0;
}
